body {
  margin: 0;
  font-family: 'Gotham', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input[type = button] {
  font-family: 'Gotham', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/****************BASE***************/

.font-link-base {
  font-family: 'Inter', sans-serif;
  color: black;
}

.font-link-bold {
  font-family: 'Inter', sans-serif;
  font-weight: 900;
}

.font-link-light {
  font-family: 'Inter', sans-serif;
  font-weight: 200;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
}

.App-big-name {
  font-size: 10vmin;
  margin-top: 2vmin;
  margin-bottom: 2vmin;
}

.App-small-name {
  font-size: 4vmin;
  margin-top: 0;
  margin-bottom: 2vmin;
  color: white;
}

.App-header-base {
  font-size: calc(10px + 2vmin);
  color: white;
  background-repeat: no-repeat;
  background-size: cover;
}

.App-header-white {
  background-image: url(/static/media/bg_white.177e2c94.svg);
}

.App-header-black {
  background-image: url(/static/media/bg.159aeebb.svg);
}

.App-main-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-buttons-container {
  min-height: 10vmin;
  display: block;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-display-flex {
  display: flex;
}

.App-button-base {
  height: 10vmin;
  margin-top: 5vmin;
  background-repeat: no-repeat;
  background-size: auto;
  background-color: transparent;
  border: none;
  background-position: center;
  cursor:pointer;
  color: white;
  font-size: 100%;
  font-family: 'Inter', sans-serif;
}

.App-button-start {
  background-image: url(/static/media/play_button.07c3d73d.svg);
  width: 30vmin;
  height: 6vmin;
  margin-top: 4vmin;
  background-size: contain;
  margin-bottom: 1vmin;
}
.App-button-start:hover {
  opacity: 0.85;
}

.App-teams-button-back {
  background-image: url(/static/media/back_button.467450b5.svg);
  background-size: contain;
}

.App-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: inherit;
}

/****************BASE***************/

.App-button-collection {
  background-size: contain;
  width: 15vmin;
  height: 5vmin;
}

.App-button-list {
  background-image: url(/static/media/list_button.4b86e57c.svg);
}
.App-button-list:hover {
  background-image: url(/static/media/list_hover.f624d13f.svg);
}

.App-button-set {
  background-image: url(/static/media/set_button.c4d0b664.svg);
}
.App-button-set:hover {
  background-image: url(/static/media/set_hover.faeafc21.svg);
}

.App-button-map {
  background-image: url(/static/media/map_button.f3c62618.svg);
}
.App-button-map:hover {
  background-image: url(/static/media/map_hover.40feff6a.svg);
}

.App-button-list-focused {
  background-image: url(/static/media/list_focused.39eb0059.svg);
}
.App-button-set-focused {
  background-image: url(/static/media/set_focused.5225cb56.svg);
}
.App-button-map-focused {
  background-image: url(/static/media/map_focused.511b7cfb.svg);
}

.App-ducks-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-ducks-row {
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: row;
}

.App-ducks-row-top {
  padding-top: 3vmin;
  padding-bottom: 0.5vmin;
}

.App-ducks-row-bottom {
  padding-top: 0.5vmin;
  padding-bottom: 2vmin;
}

.App-duck-container {
  background-image: url(/static/media/duck_box.be7f68fa.svg);
  height: 18vmin;
  width: 18vmin;
  background-size: contain;
  background-repeat: no-repeat;
  margin-left: 1vmin;
  margin-right: 1vmin;
}

.App-duck-container-best {
  background-image: url(/static/media/box_best.04fd7659.svg);
}


.App-duck-image {
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  margin-bottom: 2vmin;
  margin-right: 0;
}

.App-duck-image-alex {
  background-image: url(/static/media/alex.f5248af4.svg);
}

.App-duck-image-daniel {
  background-image: url(/static/media/daniel.d7b2d5c8.svg);
