.App-duck-container-label {
  background-image: url(/static/media/duck_label.29f4c090.svg);
  position: absolute;
  font-size: 15px;
  padding-top: 4px;
  padding-bottom: 4px;
  margin-top: -4.7vmin;
  margin-left: 0.5vmin;
  width: 8vmin;
  text-align: center;
}

.App-display-none {
  display: none;
}

.App-functions-container {
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: row;
}

.App-button-action {
  background-size: contain;
  height: 4vmin;
  width: 10vmin;
  margin: 0 0.25vmin 0.25vmin;
}

.App-button-add {
  background-image: url(/static/media/add.cca0c873.svg);
  margin-left: 0;
  margin-right: 0;
}
.App-button-add:hover {
  background-image: url(/static/media/add-hover.bad3ea55.svg);
}

.App-button-remove {
  background-image: url(/static/media/remove.5a9bab39.svg);
}
.App-button-remove:hover {
