}

.App-unclickable-button {
  pointer-events: none;
  opacity: 0.4;
}

.App-button-back {
  background-image: url(/static/media/back_button.467450b5.svg);
  background-size: contain;
  width: 20vmin;
  height: 5vmin;
  position: absolute;
  top: 1vmin;
  right: 5vmin;
}
.App-button-back:hover {
  background-image: url(/static/media/back_button_hover.c7d14fd4.svg);
}


.App-info-container {
  text-align: center;
  position: absolute;
  top: 15%;
  left: 20%;
  width: 60%;
}

.App-info-container-text {
  color: #5f5e5e;
  font-size: 2.5vmin;
  white-space: pre-line;
}

